import axios from 'axios';
import qs from 'qs';
import * as setting from '../public/config.js';
import {Message} from 'element-ui';
import router from './router';

axios.defaults.timeout = 20000;
if(process.env.NODE_ENV == "development"){
    axios.defaults.baseURL = "https://open.woyaou.com";
}else{
    axios.defaults.baseURL = "/";
}
//POST传参序列化
axios.interceptors.request.use((config) => {
    //登录的COOKIE 回头在这里加上
    config.data = qs.stringify(config.data);
    config.headers = {
        'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
    };
    return config;
}, (error) => {
    Message({
        message: '参数错误',
        type: 'warning'
    });
    return Promise.reject(error);
});

//返回状态判断
axios.interceptors.response.use((res) => {
    if (res.status == 200) {
        return res;
    } else {
        return Promise.reject(res);
    }
}, (error) => {
    Message({
        message: '网络异常',
        type: 'warning'
    });
    return Promise.reject(error);
});

export function post(url, params) {
    return new Promise((resolve, reject) => {
        axios.post(url, params)
            .then(response => {
                switch (response.data.code) {
                    case setting.WOYAOU_SUCCESS:
                        resolve(response.data.data);
                        break;
                    case setting.WOYAOU_WARRING:
                        Message({
                            message: response.data.msg,
                            type: 'warning'
                        });
                        reject(response.data.code);
                        break;
                    case setting.WOYAOU_NOLOGIN:
                        localStorage.clear();
                        sessionStorage.clear();
                        router.replace('/');
                        break;
                    case setting.WOYAOU_NOACCESS:
                        router.replace('/noaccess');
                        break;
                    default:
                        reject(response.data.code);
                        break;
                }
            }, err => {
                reject(err);
            })
            .catch((error) => {

                reject(error);
            })
    })
}
